import config from '../../global/config';

const createDaftarRestaurant = (restaurants) => `
    <a href="/#/detail/${restaurants.id}">
        <article>
            <h6>Kota: ${restaurants.city}</h6>
            <img class="lazyload" crossorigin="anonymous" data-src="${config.BASE_IMAGE_URL}/${restaurants.pictureId}" alt="${restaurants.name}" />
            <h5>⭐️ ${restaurants.rating}</h5>
            <h3 class="restaurant_name">${restaurants.name}</h3>
            <p>${restaurants.description}</p>
        </article>
    </a>
`;

const createDetailRestaurant = (restaurants) => {
  let reviewsHTML = '';

  if (restaurants.customerReviews) {
    restaurants.customerReviews.forEach((review) => {
      reviewsHTML += `
          <div tabindex="0" class="review-customer">
            <h3>${review.name}</h3>
            <h5>${review.date}</h5>
            <p>${review.review}</p>
          </div>
        `;
    });
  }

  let foodHTML = '';

  if (restaurants.menus && restaurants.menus.foods) {
    restaurants.menus.foods.forEach((food) => {
      foodHTML += `<li tabindex="0">${food.name}</li>`;
    });
  }

  let drinkHTML = '';

  if (restaurants.menus && restaurants.menus.drinks) {
    restaurants.menus.drinks.forEach((drink) => {
      drinkHTML += `<li tabindex="0">${drink.name}</li>`;
    });
  }

  const foodList = `<ul id="food-list">${foodHTML}</ul>`;
  const drinkList = `<ul id="drink-list">${drinkHTML}</ul>`;

  return `
        <h3 tabindex="0">${restaurants.name}</h3>
        <img class="lazyload" crossorigin="anonymous" data-src="${config.BASE_IMAGE_URL}/${restaurants.pictureId}" alt="${restaurants.name}" />
        <h6 tabindex="0">Kota: ${restaurants.city}</h6>
        <h6 tabindex="0">Alamat: ${restaurants.address}</h6>
        <h6 tabindex="0">⭐️ ${restaurants.rating}</h6>
        <p tabindex="0">${restaurants.description}</p>
        <div class="makanan">
          <h4 tabindex="0">Daftar Makanan</h4>
          ${foodList}
        </div>
        <div class="minuman">
          <h4 tabindex="0">Daftar Minuman</h4>
          ${drinkList}
        </div>
        <div class="ulasan">
          <h4 tabindex="0">Ulasan Pelanggan</h4>
          <div class="review-layout">${reviewsHTML}</div>
        </div>
      `;
};

const createUnLikeButton = () => `
  <button aria-label="sukai Restaurant ini" id="likeButton" class="like">
    <i aria-hidden="true"><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" class="bi bi-star" viewBox="0 0 16 16">
    <path d="M2.866 14.85c-.078.444.36.791.746.593l4.39-2.256 4.389 2.256c.386.198.824-.149.746-.592l-.83-4.73 3.522-3.356c.33-.314.16-.888-.282-.95l-4.898-.696L8.465.792a.513.513 0 0 0-.927 0L5.354 5.12l-4.898.696c-.441.062-.612.636-.283.95l3.523 3.356-.83 4.73zm4.905-2.767-3.686 1.894.694-3.957a.565.565 0 0 0-.163-.505L1.71 6.745l4.052-.576a.525.525 0 0 0 .393-.288L8 2.223l1.847 3.658a.525.525 0 0 0 .393.288l4.052.575-2.906 2.77a.565.565 0 0 0-.163.506l.694 3.957-3.686-1.894a.503.503 0 0 0-.461 0z"/>
  </svg></i>
  </button>
`;

const createLikedButton = () => `
  <button aria-label="batalkan sukai Rastaurant" id="likeButton" class="like">
    <i aria-hidden="true"><svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" class="bi bi-star-fill" viewBox="0 0 16 16">
    <path d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.282.95l-3.522 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z"/>
  </svg></i>
  </button>
`;

export {
  createDaftarRestaurant,
  createDetailRestaurant,
  createLikedButton,
  createUnLikeButton,
};
